import {
	mapState,
	mapMutations
} from 'vuex'
import mInput from '../../components/m-input.vue'
export default {
	components: {
		mInput
	},
	data() {
		return {
			account:'',
			password:''
		}
	},
	methods:{
		bindLogin(){
			if (this.account.length == 0 || this.account.length != 11) {
				uni.showToast({
					icon:'none',
					title:'请输入长度为11的手机号!'
				});
				return;
			}
			if (this.password == "" && this.password.length < 6) {
				uni.showToast({
					icon:'none',
					title:'请输入长度大于等于6位数的密码!'
				});
				return;
			}
		}
	}
}
